.modalBlock{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:no-wrap;height:100%;width:100%;background-color:rgba(0,0,0,.4);position:fixed;top:0;left:0;backdrop-filter:blur(3px);z-index:1000}.modalBlock .modalContent{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:no-wrap;width:750px;min-height:200px;border-radius:20px;background-color:#fff;position:relative}@media screen and (max-width: 1149px){.modalBlock .modalContent{width:90%}}.modalBlock .modalContent .closeBlock::after{color:#000;content:"✖";cursor:pointer;font-weight:300;font-size:20px;position:absolute;right:25px;top:20px}